



.container-basic{
display:none;
    height: auto;

    background-color: #E5E4D7;

    outline: 2px solid rgb(175, 175, 175);
    
}

  


    .question-input{


        text-align: center;
    }
    .question-input__textarea{
        width: 100%;
        resize: none;
    }
    .regular-button{
            border-radius:2px;
            font-size: 0.9rem;
            padding: 6px 12px;
            outline: none;
        
    }
        .regular-button-light{
            background: white;
            border: solid 1px #ced0d4;
            color:#4b4f56
        }

            .regular-button-light:hover{
                background: rgb(184, 184, 184);
            }
            .regular-button-light:active{
                background: rgb(167, 167, 167);
                border: solid 1px #6b6b6b;
                outline: none;
            }
    .questions{
        scroll-behavior: smooth;

        background-color: #E5E4D7;
        width: 100%;
        height: 50vh;
        overflow: auto;
       
    }
    @media screen and (max-width: 800px) {
        .container-question{
 
        }

    }

    .question-display{
        cursor: pointer;
        
    }
    .question-display:hover{
        background-color: wheat;
        border: thin 2px wheat;
    }
    .question-display span{
        border-radius: 50%;
        line-height: 2rem;
        color: white;
        background-color:#92CD00;
        display: inline-block;
        width: 2rem;
        height: 2rem;
       text-align: center;
    }
::-webkit-scrollbar {
    width: 5px;
    
  }
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
  
  }
  ::-webkit-scrollbar-thumb {
   
    background: black; 
    border-radius: 3px;
  }
.container-active{
display: block;
}