
body{
  background-color: #E5E4D7;
}
.navbar{
  background-color:rgb(191, 202, 153);
  border: solid 1px rgb(0, 0, 0)
}
.note{
  height: auto;
  display: grid;
  margin: 40px 0 0px 0;
  padding: 5px;
  grid-template-columns: 2fr 5fr;
  grid-template-rows: 1fr 5fr;

  min-height: 0;  /* NEW */
  min-width: 0;   /* NEW; needed for Firefox */

  grid-template-areas: "question video " "question video ";
  
  overflow: hidden;
}
.question-root{
  grid-area: question;
  margin: 0;
}
.information-root{
  grid-area: information;
  background-color: palegoldenrod;
  margin: 0;
}
.video-root{
  grid-area: video;
  width: 100%;
  height: 100%;

  display: grid;
  grid-template-rows: 2fr 6fr;
  padding: 0px;
}

.section-root{
  grid-area: section;
  width: 100%;
  margin: 0;
}
h5{
  text-align: center;
  font-weight: 800;
  font-size: 1.3rem;

  margin: 0;
}