.sections{
    scroll-behavior: smooth;
    padding: 0.5em;
    background-color: #E5E4D7;
    width: 100%;
    height: 100%;
    overflow: auto;
    border: rgb(190, 188, 159) 1px transparent;
    border-bottom:  rgb(190, 188, 159) 1px solid;
    display:grid;
    
    justify-items: center;
    align-items: center;

    grid-gap: 2em;
    grid-auto-rows: max-content;
}
.section-display{
    display: grid;
    align-items: start;
    padding: 2px;
    grid-gap: 1em;
    grid-template-columns: 5fr 8fr;
    
}
.section-thumnail{
   width: 100%;
   height: 100%;
}