.container-information{



    display: inline-block;

}
.informations{
    transition: height 0.2s ease-out;
    scroll-behavior: smooth;
    padding: 0 18px;
    margin: 0;
    background-color: #E5E4D7;
    width: 100%;
    height: 0;

    overflow-y: hidden;
    border: rgb(190, 188, 159) 1px transparent;


}
.informations::before{

    content:"";
    border: 20px solid transparent;
    padding: 0;
    margin: 0;
    width: 0;
    height: 0;
    border-bottom: 20px solid rgb(165, 165, 165);
    position: absolute;
    bottom: 0;


}
.informations--expanded{
    overflow-y: hidden;
    border-bottom:  rgb(190, 188, 159) 1px solid;
    max-height: 50vh;
    background-color: rgba(238, 232, 170, 0.349);
}
